.doneBtn{
    height: 30px;
    border: 1px solid rgb(126, 126, 126, 0.5);
    border-radius: 50%;
    text-align: center;
    flex: 0 0 30px
}

.doneBtn img{
    max-width: 100%;
    height: 100%;
}
.deletebtn{
    color: rgb(226, 59, 92);
    font-size: 20px;
    width: 30px;
    height: 30px;
    text-align: center;
    opacity: 0;
    transition: opacity 0.5s ease;
}

.task:hover .deletebtn {
    opacity: 0.8;
}

.task {
    display: flex;
    align-items: center;
    font-size: 20px;
    padding: 10px;
    border-bottom: 1px solid rgb(141, 139, 139, 0.3);
}
.taskAndBtn{
    max-width: 92%;
    flex: 1 0 auto;
    padding-left: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    word-break:break-all;  
}
.inputTask{
    max-width: 100%; 
    display: flex;
    align-items: center;
    padding: 10px 10px 10px 15px;
    border-bottom: 1px solid rgb(126, 126, 126, 0.5);
}
.inputTask input,
.changingInput{
    width: 89%;
    padding: 10px;
    font-size: 20px;
    outline: none;
    border: none;
}
.changingInput{
    border: 1px solid rgb(126, 126, 126, 0.5);
    margin-left: 45px;
}
.icon{
    color: rgb(182, 179, 179, 0.3);
    font-size: 18px;
    cursor: pointer;
}
.iconActive{
    color: rgb(182, 179, 179, 1);
}
.categories {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    color: rgb(126, 126, 126);
    padding: 0 20px;
}
.categories p{
    min-width: 70px;
}
.categories span{
    display: inline-block;
    margin: 0 5px;
    padding: 3px 5px;
    border: 1px solid rgb(223, 79, 79, 0);
    cursor: pointer;
}
span.active,
.categories span:hover{
    border: 1px solid rgb(223, 79, 79, 0.2)
}
.complited{
    color: rgb(172, 169, 169);
    text-decoration: line-through;
}
.deleteCompletedBtn{
    cursor: pointer;

}
.deleteCompletedBtn:hover{
    border-bottom: 1px solid rgb(126, 126, 126, 0.5);
}
.invisible{
    visibility: hidden;
}